.featured-projects {
  margin-bottom: 5rem;
}
.featured-projects h2 {
  font-family: "acumin-pro", sans-serif;
  color: #011F26;
  background-color: #fff;
}
.featured-projects .featured-projects-heading {
  font-size: 0.875rem;
  letter-spacing: 0.0625rem;
}
.featured-projects .featured-projects-line {
  border-top: 2px solid #99C9D6;
  opacity: 1;
}
.featured-projects .featured-project {
  text-decoration: none;
  border-radius: 3rem 0 0 0;
  min-height: 25rem;
}
.featured-projects .featured-project:hover .featured-project-image img, .featured-projects .featured-project:focus .featured-project-image img {
  transform: scale(1.05);
}
.featured-projects .featured-project:hover .featured-project-excerpt, .featured-projects .featured-project:focus .featured-project-excerpt {
  max-height: 18.75rem;
  opacity: 1;
}
.featured-projects .featured-project-image {
  height: 25rem;
}
.featured-projects .featured-project-image img {
  transition: transform 0.5s ease-in-out;
}
.featured-projects .featured-project-placeholder {
  height: 25rem;
}
.featured-projects .featured-project-overlay {
  background: linear-gradient(to top, rgba(0, 0, 0, 0.8) 0%, rgba(0, 0, 0, 0.4) 30%, rgba(0, 0, 0, 0) 70%);
}
.featured-projects .featured-project-content h3 {
  color: #fff;
}
.featured-projects .featured-project-content .featured-project-excerpt {
  max-height: 18.75rem;
  opacity: 1;
  overflow: hidden;
  transition: max-height 0.4s ease, opacity 0.4s ease;
  color: #fff;
}
@media screen and (min-width: 768px) {
  .featured-projects .featured-project-content .featured-project-excerpt {
    max-height: 0;
    opacity: 0;
  }
}